// Imports -------------- //

// Begin uswds homepage.scss

// Variables ------------ //

$home-text-width:      660px;
$home-section-padding: 7.2rem;

$styleguide-width-nav-sidebar:  250px;
$styleguide-color-grid-dark:    #727272;
$styleguide-color-grid-light:   #e3e4e5;
$styleguide-site-top:           164px;

.skip {
  margin: 0;
  padding:0;
}

.disclaimer {
  padding: .5rem 2rem;
  color: white;
  text-align: center;
  font-size: $base-font-size / 1.25;
  border-bottom: 1px solid #616161;
  font-weight: 100;
  .us-official {
    margin: 0;
  }
  .stage {
    a { text-decoration: underline; }
    float: none;
    display: block;
    padding-top: 5px;
  }
  @include media($medium-screen + $styleguide-width-nav-sidebar) {
    .us-official {
      margin-left: 177px;
    }
    .stage {
      float: right;
      padding: 0;
    }
  }
}

section {
  padding-top: 1rem;
  padding-bottom: 1.5rem;
}

section:nth-of-type(4) {
  margin-top: 1rem;
}

.alt {
  background-color: $iae-color-gray;
}

.usa-banner {
  font-size: $base-font-size;
  text-align: center;
  font-weight: 300;
  font-size: $base-font-size;

  @include media($medium-screen) {
    font-size: $h2-font-size;
  }

  p:nth-of-type(1) {
    line-height: 2rem;

    @include media($medium-screen) {
      line-height: 3rem;
    }
  }

  h1 {
    margin-top: 1rem;
    font-size: $h1-font-size;
    font-weight: 300;
  }

  b {
    font-weight: 400;
  }
}

#style-home {

  .usa-grid {
    .usa-width-one-third blockquote {
      margin: 0;
      padding-left: 0;
      padding-bottom: 1rem;
      border-top: solid 2px $iae-color-gray;
      border-left: none;
    }

    .usa-width-one-third > p {
      height: 80px;
      text-align: center;
      background-size: 200px auto;
      background-repeat: no-repeat;
      background-position: center bottom;

      &.bourbon {
        background-image: url('../img/img_styleguide/bourbon-logo@2x.png');
      }

      &.bourbon-neat {
        background-image: url('../img/img_styleguide/bourbon_neat.png');
        background-size: 125px auto;
      }

      &.jquery {
        background-image: url('../img/img_styleguide/jQuery-Logo.png');
        background-size: 150px auto;
      }

      &.usa-ui-components {
        background-image: url('../img/img_styleguide/homepage_illustrations_ui_components_2x.png');
        background-size: 90px auto;
      }

      &.sass {
        background-image: url('../img/img_styleguide/sass-logo.png');
        background-size: 80px auto;
      }

      &.npm {
        background-image: url('../img/img_styleguide/npm.png');
        background-size: 150px auto;
      }

      &.uswds {
        background-image: url('../img/img_styleguide/uswds.png');
        background-size: 80px auto;
      }      

      &.font-awesome {
        background-image: url('../img/img_styleguide/fontawesome.png');
        background-size: 110px auto;
      }

      &.uikit {
        background-image: url('../img/img_styleguide/uikit.png');
        background-size: 120px auto;
      } 
    }
  }
}

// Begin uswds styleguide.scss

// Variables -------------- //


// Navigation ------------- //

#styleguide {
  .usa-site-header {
    //@include position(fixed, 0 null null 0);
    background-color: $color-white;
    border-bottom: 1px solid $color-gray-light;
    width: 100%;
    z-index: 1;

    a {
      border-bottom: none;
    }

    .usa-site-navbar {
      @include media($medium-screen + $styleguide-width-nav-sidebar) {
        @include margin(2.7rem 0 1rem);
        height: 6rem;
        background-color: black;
      }
      //margin-top: 4px;

      .logo {
        @include media($medium-screen + $styleguide-width-nav-sidebar) {
          @include padding(0 null null $site-margins);
          text-align: left;
          width: auto;
        }
        float: left;
        text-align: center;
        padding-top: 1.3rem;
        width: 70%;

        a {
          color: $color-gray;

          &:hover {
            color: $color-gray-dark;
            text-decoration: none;
          }
        }
      }

      h1 {
        @include media($medium-screen + $styleguide-width-nav-sidebar) {
          display: block;
          font-size: $h3-font-size;
          margin-top: .9rem;
        }
        margin: 0;
        font-size: $h5-font-size;
      }

      .usa-button-list {
        @include media($medium-screen + $styleguide-width-nav-sidebar) {
          display: block;
          padding-right: $site-margins;
        }
        float: right;
        display: none;
        margin-top: -5px;

        li {
          display: inline;
          font-family: $font-sans;

          &:last-child .usa-button {
            margin-right: 0;
          }
        }
      }
    }
  }

  .menu-btn {
    @include media($medium-screen + $styleguide-width-nav-sidebar) {
      display: none;
    }
    @include padding(1.5rem null);
    display: inline;
    float: left;
    margin-top: -4px;
    color: $color-white;
    background-color: $color-primary;
    font-size: 1.5rem;
    width: 15%;
    text-align: center;

    &:hover {
      text-decoration: none;
      color: $color-white;
      background-color: $color-primary-darker;
    }

    &:visited {
      color: $color-white;
    }
  }

  #menu-content {
    @media (max-width: $medium-screen + $styleguide-width-nav-sidebar) {
      $sliding-panel-width: 220px;

      @include position(fixed, 0 auto 0 0);
      @include size($sliding-panel-width 100%);
      @include transform(translateX(- $sliding-panel-width));
      background: #fff;
      -webkit-overflow-scrolling: touch;
      overflow-y: auto;
      z-index: 999999;
      display: block;
      &.is-visible {
        @include transition(all 0.25s linear);
        @include transform(translateX(0));
      }
    }
  }

  .overlay {
    @include position(fixed, 0);
    @include transition;
    background: #000;
    opacity: 0;
    visibility: hidden;
    z-index: 9999;

    &.is-visible {
      visibility: visible;
    }
  }

  // Based on code by Diego Eis

  // Sidebar Nav --------- //

  .sidenav {
    @include position(absolute, $styleguide-site-top null 0 10rem);
    width: $styleguide-width-nav-sidebar;
    //border-right: 1px solid $color-gray-light;
    padding: 0rem 3rem 0rem 0rem;
    overflow: auto;
    display: none;
    //z-index: -1;

    &.fixed {
      @include position(fixed, 5em null 0 10rem);
    }

    .lt-ie9 & {
      width: 25%;
    }

    .usa-sidenav-sub_list {
      display: none;
    }

    a.usa-current + .usa-sidenav-sub_list {
      display: block;
    }

    @include media($medium-screen + $styleguide-width-nav-sidebar) {
      display: block;
    }
  }

  .visual-style .sidenav .visual-style-sublist {
    display: block;

    ul {
      display: block;
    }
  }

  .form-controls .sidenav .form-controls-sublist {
    display: block;
  }

  .form-templates .sidenav .form-templates-sublist {
    display: block;
  }

  .footers .sidenav .footers-sublist {
    display: block;
  }

  // Main Content --------- //

  .main-content {
    @include media($medium-screen) {
      top: $styleguide-site-top;
    }
    @include position(absolute, 4rem 0 0 null);
    @include padding(1em null 0);
    width: 100%;
    z-index: -1;
    .lt-ie9 & {
      width: 75%;
    }

    @include media($medium-screen + $styleguide-width-nav-sidebar) {
      width: calc(100% - #{$styleguide-width-nav-sidebar});
      .lt-ie9 & {
        width: 75%;
      }
    }
  }

  // Styleguide Content -------- //

  .styleguide-content {
    margin-bottom: 5em;
    max-width: $site-max-width;
    @include padding(null 2em);
    position: relative;

    > h1 {
      margin-top: 3.4rem;
      &:not(:first-child) {
        margin-top: 1.5em;
      }
    }
    @media (min-width: $medium-screen + $styleguide-width-nav-sidebar) {
      @include padding(null 3em);
    }
  }

  // Footer --------------- //
/*
  .usa-styleguide-footer {
    background-color: $color-primary-darker;
    @include padding(3rem null);

  // This is a styleguide-only rule and is not needed in the main library code for
  // the footer component which uses different styles
  // TODO investigate why it's not needed in the main library CSS
    p, a {
      @include media($medium-screen) {
        margin-bottom: 0;
      }
      color: $color-white;
      font-size: $h5-font-size;
      @include margin(0 null 1.5rem);
    }
  }
*/
  // Pattern Preview Boxes -------- //

  .preview {
    @include clearfix();
    background-color: #fff;
    @include margin(2em null);
    padding: 1em $site-margins;
    border: 1px solid #eeeeee;
    .is-peripheral {
      opacity: .2;
    }
  }

  .preview-no_border {
    border: 0;
    @include margin(0 null 2em);
    padding: 0;
  }

  // Code Sample Boxes --------- //

  .code-snippets {

    .code-snippet-button:after {
      content: "\25be  hide code";
    }

    &.hidden {

      table {
        display: none;
      }

      .code-snippet-button:after {
        content: "\25b8  show code";
      }
    }

    table {

      table-layout: fixed;
      width: 100%;
      padding: 3em 0 0 0;

      td {
        background-color: #f5f5f5;
        border: 1px solid #e0e0e0;
        position: relative;
        vertical-align: top;

        .code-copy-button {
          @include position(absolute, 0 0 null null);
          border-radius: 0;
          margin: 0;

          &:hover {
            background-color: $color-base;
          }
        }
      }

      th {
        border: none;
        text-align: left;
      }

      pre, code {
        direction: ltr;
        text-align: left;
        white-space: pre;
        word-spacing: normal;
        word-break: normal;
      }

      pre {
        padding: 0 1em 1em 1em;
      }

      code {
        font-size: $base-font-size / 1.5;
        line-height: 1.4em;
        word-wrap: break-word;

        &.language-html {
          max-width: 25em;
        }
      }
    }
  }

  // Download Code Boxes --------- //

  .download-code {
    padding-top: 3em;
  }

  // Search bar grid --------- //

  .preview-search-bar {
    .usa-grid {
      padding: 0;
    }
  }

  // Custom styles to illustrate invisible grid for web design standards

  .grid-example {
    .usa-grid {
      background: $styleguide-color-grid-light;

      > * {
        color: $color-white;
        padding: 1em;
        background: $styleguide-color-grid-dark;
        @include margin(.5em null);
      }
    }

    .usa-width-one-twelfth {
      @include media($medium-screen) {
        @include padding(null 0);
      }
    }
  }

  .grid-example-blank {
    .usa-grid {
      text-align: center;
    }
  }

  .grid-text {
    .usa-grid {
      background: $styleguide-color-grid-dark;

      > * {
        background: $styleguide-color-grid-light;
        color: #000;
      }

      h3 {
        margin-top: 0;
      }

      p {
        margin-bottom: 0;
      }
    }
  }

  h3 + .button_wrapper {
    margin-top: -.5em
  }

  .button_wrapper {
    clear: both;
    display: block;
    margin-left: -1rem;
    padding: 0rem 1rem;

    @include media($small-screen) {
      display: table;
    }

    button:last-child {
      margin-right: 0;
    }
  }

  .button_wrapper-dark {
    background: #323a45
  }
