    .usa-display-alt {
      font-size: rem(44px);
      font-weight: $font-light;
    }

    .usa-font-lead {
      font-family: $font-sans;
      font-size: rem(22px);
      font-weight: $font-light;
      line-height: $base-line-height;
    }

    &.serif-body {
      .usa-font-example {
        p {
          font-family: $font-serif;
          font-size: rem(15px);
          line-height: $lead-line-height;
        }

        .usa-font-lead {
          font-family: $font-sans;
          font-size: rem(22px);
          font-weight: $font-light;
          line-height: $base-line-height;
        }
      }
    }
  }

  .serif-sans-minor {
    h6 {
      font-family: $font-sans;
    }
  }

  .usa-accordion-docs {
    margin-bottom: 6rem;
  }

  // This adds styleguide-only right and left margins for our disclaimer
  // Since the layout we use is not in a grid
  .usa-disclaimer-stage {
    padding-right: $site-margins;
  }

  .usa-disclaimer-official {
    @include media($medium-screen) {
      padding-left: $site-margins;
    }
    padding-left: 2rem;
  }

  .footnotes {
    border-top: 1px solid $color-black;

    ol, p {
      margin: 0;
      padding: 0;
    }

    ol {
      margin-top: 2rem;
      font-size: 1.25rem;
    }
  }
}
